.Button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 5px;
  border: 1px solid #ccc;
  color: #000;
  margin-right: 15px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  margin-bottom: 5px;
}
.Button:focus {
  outline: none;
}
.Button:active {
  box-shadow: inset 2px 2px 1px rgba(0,0,0,.3);
}
.Button:disabled {
  background-color: #ccc;
  color: #000;
  cursor: not-allowed;
}
.error {
  background-color: rgba(240, 87, 108, 1);
}
.success{
  background-color: rgba(161, 240, 69, 1);
}
.primary{
  background-color: #2884f6;
  color: #fff;
}